.re-item[data-v-0a118152] {
  width: 220px;
  margin: 50px auto;
  height: 276px;
  cursor: pointer;
  position: relative;
  perspective: 500;
  -webkit-perspective: 500;
}
.re-item img[data-v-0a118152] {
  max-width: 220px;
}
.re-item-front[data-v-0a118152],
.re-item-back[data-v-0a118152] {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  -webkit-perspective: 1000;
  /*backface-visibility: hidden;*/
  -webkit-transition: all 1.5s;
  -moz-transition: all 1.5s;
  -ms-transition: all 1.5s;
  -o-transition: all 1.5s;
  -webkit-box-shadow: rgba(50, 50, 50, 0.2) 0 0 15px;
          box-shadow: rgba(50, 50, 50, 0.2) 0 0 15px;
  overflow: hidden;
}
.re-item-back[data-v-0a118152] {
  position: relative;
  transform: rotateY(-180deg);
  -webkit-transform: rotateY(-180deg);
}
.re-item:hover .re-item-front[data-v-0a118152] {
  transform: rotateY(-180deg);
  -webkit-transform: rotateY(-180deg);
}
.re-item:hover .re-item-back[data-v-0a118152] {
  transform: rotateY(-360deg);
  -webkit-transform: rotateY(-360deg);
}
